:root,
[data-theme=light] {
  --color-bg: #efefef;
  --color-txt: #0e0b16;
  --color-txt-contrast: #e9e8e8;
  --color-primary: #ff3b3f;
  --color-secondary: #caebf2;
}

[data-theme=dark] {
  --color-bg: #0e0b16;
  --color-txt: #e7dfdd;
  --color-txt-contrast: #e7dfdd;
  --color-primary: #4717f6;
  --color-secondary: #a239ca;
}

/* try to use mobile first strategy */
/* reset */
/**
* image 
**/
.img-full-width {
  width: 100%;
}

/**
* text align
**/
.text-align-left {
  text-align: left;
}

.text-align-right {
  text-align: right;
}

.text-align-center {
  text-align: center;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

