body {
  background: var(--color-grey-e);
  color: var(--color-grey-2);
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  margin: 0;
  padding: 0;
  color: inherit;
}

h1,
.h1 {
  font-size: 3.4375rem;
  font-weight: 100;
}

h2,
.h2 {
  font-size: 2.8125rem;
  font-weight: 700;
}

h3,
.h3 {
  font-size: 1.875rem;
  font-weight: 700;
}

h4,
.h4 {
  font-size: 1.25rem;
  font-weight: 700;
}

h5,
.h5 {
  font-size: 0.875rem;
  font-weight: 700;
}

h6,
.h6 {
  font-size: 0.625rem;
  font-weight: 700;
}

p {
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 1.5;
}

a {
  color: inherit;
}

.header {
  background-color: var(--color-bg);
  font-weight: 700;
  padding: 1rem 0;
}

.accordion {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
@media screen and (min-width: 992px) {
  .accordion {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}

.accordion-tab {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: 1.25rem 0.4375rem 1.25rem 0;
  width: 100%;
  border: 0;
  appearance: none;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
  line-height: 1;
  text-align: left;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
  color: var(--color-text);
}
.accordion-tab::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 1px;
  background-color: var(--color-primary);
  transition: height 0.15s ease-out;
}
.accordion-tab:focus-visible {
  outline: auto;
}
@media (hover: hover) {
  .accordion-tab:hover::after {
    height: 5px;
  }
}
@media screen and (min-width: 992px) {
  .accordion-tab {
    padding: 1.875rem 0.4375rem 1.875rem 0;
    font-size: 1.5rem;
  }
}
.accordion-tab-label {
  position: relative;
  width: calc(100% - 100px);
  line-height: 1.15;
}
.accordion-tab-icon {
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
}
.accordion-tab-icon::before {
  content: "";
  position: absolute;
  width: 0.25rem;
  height: 1.5rem;
  left: 0.625rem;
  top: 0;
  background-color: var(--color-primary);
  transform-origin: center;
  transform: rotate(0);
  transition: transform 0.3s;
}
.accordion-tab-icon::after {
  content: "";
  position: absolute;
  width: 1.5rem;
  height: 0.25rem;
  top: 0.625rem;
  left: 0;
  background-color: var(--color-primary);
}
.accordion-tab.expanded .accordion-tab-icon::before {
  transform: rotate(90deg);
  left: 10px;
}

.accordion-panel {
  position: relative;
  height: 0;
  top: 1px;
  outline: none;
}
.accordion-panel > div {
  position: relative;
  padding-top: 1.5rem;
  padding-bottom: 2.5rem;
  padding-right: 10%;
  opacity: 0;
}
.accordion-panel > div::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background: var(--color-btn);
}
.accordion-panel.expanded > div {
  opacity: 1;
}

.accordion-panel.animate-in {
  transition: height 0.3s ease-out;
}
.accordion-panel.animate-in > div {
  transition: opacity 0.4s ease-out 0.2s;
}
.accordion-panel.animate-out {
  transition: height 0.3s ease-out 0.2s;
}
.accordion-panel.animate-out > div {
  transition: opacity 0.3s ease-out;
}

.button {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  cursor: pointer;
  line-height: 1;
  box-shadow: none;
  appearance: none;
  border-radius: 3px;
  background-color: var(--color-green);
  color: #fff;
  padding: 0.8125rem 0.625rem;
  font-size: 0.875rem;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
}
.button:hover {
  background-color: rgba(var(--color-green), 60%);
}
.button:focus {
  outline: 2px solid var(--color-blue);
}
.button.disabled, .button[disabled] {
  background-color: var(--color-grey-5);
  color: var(--color-grey-8);
  cursor: default;
}

.hero {
  height: 100vh;
  background: #000;
  color: var(--color-white);
  display: flex;
  justify-content: left;
  align-items: center;
}
.hero h2 {
  font-size: 200px;
}
.hero .line1 {
  padding-left: 50%;
}

.sticky {
  position: relative;
}

.sticky-bar {
  width: 100%;
  height: 0;
  position: sticky;
  top: calc(100% - 30px);
  z-index: 1000;
}

.sticky-bar-orange {
  background-color: var(--color-grey-green);
  position: absolute;
  width: 1px;
  height: 30px;
  top: 0;
  left: 0;
}

.sticky-bar-black {
  background-color: var(--color-black);
  position: absolute;
  width: 100%;
  height: 30px;
  top: 0;
  left: 0;
}

.slide {
  position: relative;
  background: var(--color-black);
}

.bg-sticky {
  background-color: var(--color-black);
  color: var(--color-grey-5);
  font-size: 12px;
  height: 100vh;
  width: 100%;
  position: sticky;
  top: 0;
  overflow: hidden;
}

.slide1-slide {
  height: 100vh;
  width: 100%;
  color: var(--color-white);
  font-size: 120px;
  position: relative;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.slide2 {
  padding: 100px 0;
  overflow: hidden;
}

.text {
  font-size: 100px;
  line-height: 1.3;
  font-weight: bold;
  color: var(--color-white);
  transform-origin: left center;
}

.slide3 {
  position: relative;
}

.slide3-slide {
  position: sticky;
  height: 100vh;
  width: 100%;
  font-size: 100px;
  overflow: hidden;
  top: 0;
  color: var(--color-white);
  text-align: center;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide3-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.mosaic1 {
  height: 15%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity ease-out 0.2s;
}

.slide3-img {
  height: 100vh;
  background-color: rgba(168, 243, 161, 0.1);
}

.slide4-slide {
  font-size: 100px;
  font-weight: bold;
  color: var(--color-white);
  height: 100vh;
  display: flex;
  align-items: center;
}

.slide4-content {
  width: 60%;
  margin-top: -300px;
}

.slide4-sticky {
  width: 30%;
  position: sticky;
  top: 0;
  margin-left: 70%;
  font-size: 60px;
  color: var(--color-white);
  padding: 100px 0;
}

.beyond {
  min-height: 700px;
  padding-top: 100px;
}

.footer {
  background-color: var(--color-bg);
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 1rem 0;
  font-size: 10px;
}

section,
.section {
  display: block;
  position: relative;
}

.section-mg {
  margin-top: 60px;
  margin-bottom: 60px;
}

.row {
  margin-bottom: 60px;
